.start__wrapper{
    display: flex;
    justify-content: space-between;
    column-gap: 3rem;
    align-items: center;
}
.start__img, 
.start__content{
    width: 50%;
}

.start__img img{
    width: 100%;
}

.start__content h2{
    font-size: 2.5rem;
    margin-bottom:20px;
}

.start__content p{
    color:var(--heading-color);
    font-size:1.1rem;
    line-height:28px;
    margin-bottom: 50px;
}